<template>
  <div id="shunfengOrder">
    <h1 style="text-align:center;color:#fff;margin:10px;">顺丰订单号</h1>
    <el-table border :data="tableData" style="width: 100%">
      <el-table-column prop="order_id" label="数据库订单号" width="180">
      </el-table-column>
      <el-table-column prop="sf_order_id" label="顺丰订单号" width="180">
      </el-table-column>
      <el-table-column prop="country" label="国家"> </el-table-column>
      <el-table-column prop="site" label="站点"> </el-table-column>
      <el-table-column prop="dates" label="时间"> </el-table-column>
      <el-table-column prop="remark" label="备注">
        <template slot-scope="scope">
          <el-input
            @change="submitRemark(scope.row)"
            v-model="scope.row.remark"
          ></el-input>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: []
    };
  },
  methods: {
    submitRemark(rowData) {
      let url = this._url2 + "server/order_management/sf_order_id_remark/";
      this.$axios
        .get(url, {
          params: {
            remark: rowData.remark,
            sf_order_id: rowData.order_id
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message.success("请求成功");
          } else {
            this.$message.warning("请求成功");
          }
        });
    },
    getTableData() {
      let url = this._url2 + "server/order_management/sf_update_order_id/";
      this.$axios.get(url).then(res => {
        if (res.data.code !== 200) {
          this.$message.warning("获取数据失败");
          return;
        }
        this.tableData = res.data.data;
      });
    }
  },
  created() {
    this.getTableData();
  }
};
</script>

<style></style>



